import { useState } from "react";
import "../App.css";
import {send} from "@emailjs/browser";
import styles from "./Contact.module.css"
function Form(){
    const [toSend, setToSend] = useState({
        yourName: "",
        yourMessage: ""
    })
    const handleChange = (e)=>{
        setToSend({...toSend,[e.target.name]:e.target.value});
    }
    const handleSubmit = (e)=>{
        e.preventDefault();
        send(
            'service_1sb3u9q',
            'template_z1o5q7l',
            toSend,
            'eKhRpvx8pr_y5HNkg'
          )
            .then((response) => {
              console.log('SUCCESS!', response.status, response.text);
              setToSend({
                  yourName:"",
                  yourMessage:""
              })
            })
            .catch((err) => {
              console.log('FAILED...', err);
            });
    }
    return (
        <form className={styles.contactForm} 
        onSubmit={handleSubmit}
        name="EmailForm">
            <input name="yourName" placeholder="Your Name" style={{width:"100%"}} value={toSend.yourName} onChange={handleChange}/>
            <textarea name="yourMessage" placeholder="Message you want to send me" style={{width:"100%"}} rows="10" value={toSend.yourMessage} onChange={handleChange}/>
            <button className="primary-btn">Submit</button>
        </form>
    )
}
export default Form;